import { buildSpeech as jBuildSpeech } from "@nmemonica/voice-ja";
import { VOICE_KIND_JA } from "../constants/voiceConstants";
import { exceptionToErrorObj } from ".";
const wSelf = globalThis.self;
wSelf.addEventListener("message", messageHandler);
function messageHandler(event) {
    const data = event.data;
    const { uid, index, tl: language, q: query, japaneseVoice } = data;
    if (language === "ja" && query !== null && typeof jBuildSpeech === "function") {
        let voice_model;
        switch(japaneseVoice){
            case "default":
            case undefined:
                voice_model = VOICE_KIND_JA.NEUTRAL;
                break;
            default:
                voice_model = japaneseVoice;
        }
        try {
            const { uid: resUid, index: resIndex, buffer: resBuffer } = jBuildSpeech(uid, index, query, voice_model);
            const response = {
                uid: resUid,
                index: resIndex,
                buffer: resBuffer
            };
            wSelf.postMessage(response);
        } catch (exception) {
            const error = exceptionToErrorObj(exception, "voice-worker-ja");
            wSelf.postMessage(error);
        }
    }
}
